import React, { useEffect, useState, useRef, useMemo } from 'react';
import { useNavigate } from 'react-router-dom';
import * as d3 from 'd3';
import { GraphData, CodeNode } from '../types';
import { fetchGraphData } from '../services/dataServices';

interface SimulationNodeWithType extends d3.SimulationNodeDatum {
  id: string;
  type: string;
  name: string;
}

interface SimulationLinkWithType extends d3.SimulationLinkDatum<SimulationNodeWithType> {
  type: string;
  source: string | SimulationNodeWithType;
  target: string | SimulationNodeWithType;
}

interface PositionCache {
  collapsedNodes: string[];                 
  positions: Map<string, { x: number; y: number }>; 
  visibleNodeIds: Set<string>;             
}

const VisualizationPage: React.FC = () => {
  const svgRef = useRef<SVGSVGElement>(null);
  const containerRef = useRef<HTMLDivElement>(null);
  const nodeMapRef = useRef<Map<string, CodeNode>>(new Map());

  const [data, setData] = useState<GraphData>({ nodes: [], links: [] });
  const [isRadial, setIsRadial] = useState<boolean>(false);
  const [isHierarchical, setIsHierarchical] = useState<boolean>(false);
  const [searchTerm, setSearchTerm] = useState<string>('');
  const width = window.innerWidth;
  const height = window.innerHeight;

  const positionCache = useRef<PositionCache>({
    collapsedNodes: [],
    positions: new Map(),
    visibleNodeIds: new Set()
  });
  
  const [collapsedNodes, setCollapsedNodes] = useState<Set<string>>(new Set());
  const [loading, setLoading] = useState<boolean>(true);
  const [error, setError] = useState<string | null>(null);
  const navigate = useNavigate();
  
  const simulationRef = useRef<d3.Simulation<any, any> | null>(null);
  const gRef = useRef<d3.Selection<SVGGElement, unknown, null, undefined> | null>(null);
  const zoomRef = useRef<d3.ZoomBehavior<SVGSVGElement, unknown> | null>(null);
  
  const forceCluster = () => {
    const strength = 0.15;
    let nodes: any[];
    let moduleGroups: {[key: string]: {x: number, y: number, children: Set<string>}} = {};
    let nodeToModuleMap: Map<string, string> = new Map();
    let moduleGroupsComputed = false;
    
    function initModuleGroups() {
      if (moduleGroupsComputed) return;
      
      moduleGroups = {};
      nodeToModuleMap.clear();
      
      data.nodes.forEach(node => {
        if (node.type === 'module') {
          moduleGroups[node.id] = { x: 0, y: 0, children: new Set() };
        }
      });
  
      data.links.forEach(link => {
        if (link.type === 'contains') {
          const sourceId = typeof link.source === 'object' ? link.source.id : link.source;
          const targetId = typeof link.target === 'object' ? link.target.id : link.target;
          
          const module = moduleGroups[sourceId];
          if (module) {
            module.children.add(targetId);
            nodeToModuleMap.set(targetId, sourceId);
          }
        }
      });
      
      moduleGroupsComputed = true;
    }
    
    function force(alpha: number) {
      initModuleGroups();
      
      for (let i = 0; i < nodes.length; i++) {
        const node = nodes[i];
        const moduleId = nodeToModuleMap.get(node.id);
        
        if (moduleId) {
          const group = moduleGroups[moduleId];
          node.vx += (group.x - node.x) * strength * alpha;
          node.vy += (group.y - node.y) * strength * alpha;
        }
      }
    }
    
    force.initialize = function(_nodes: any[]) {
      nodes = _nodes;
      moduleGroupsComputed = false;
    };
    
    return force;
  };

  const normalizedNodes = useMemo(() => {
    return data.nodes.map(node => ({ ...node }));
  }, [data.nodes]);
  
  const normalizedLinks = useMemo(() => {
    return data.links.map(link => ({
      source: typeof link.source === 'object' ? link.source.id : link.source,
      target: typeof link.target === 'object' ? link.target.id : link.target,
      type: link.type
    }));
  }, [data.links]);
  
  useEffect(() => {
    const worker = new Worker(new URL('../worker/simulationWorker.ts', import.meta.url));
    worker.postMessage({ nodes: normalizedNodes, links: normalizedLinks, width, height });
    worker.onmessage = (event) => {
      setData(prev => ({ ...prev, nodes: event.data.nodes }));
    };
    return () => worker.terminate();
  }, [normalizedNodes, normalizedLinks, width, height]);

  useEffect(() => {
    const loadData = async () => {
      try {
        setLoading(true);
        const graphData = await fetchGraphData();
        setData(graphData);
        setError(null);
        const nodeMap: Map<string, CodeNode> = new Map();
        graphData.nodes.forEach(node => {
          nodeMap.set(node.name.toLowerCase(), node);
        });
        nodeMapRef.current = nodeMap; 
      } catch (err) {
        console.error('Error loading graph data:', err);
        setError('Failed to load data. Please try again later.');
      } finally {
        setLoading(false);
      }
    };
  
    loadData();
  }, []);

  const resetZoom = () => {
    if (svgRef.current && zoomRef.current && gRef.current) {
      const zoom = zoomRef.current as unknown as d3.ZoomBehavior<Element, unknown>;
      d3.select(svgRef.current)
        .transition()
        .duration(750)
        .call(zoom.transform as any, d3.zoomIdentity);
    }
  };

  const toggleLayout = () => {
    setIsRadial(!isRadial);
    
    if (simulationRef.current) {
      const simulation = simulationRef.current;
      
      if (!isRadial) {
        simulation.force('x', null);
        simulation.force('y', null);
        simulation.force('r', d3.forceRadial(250, width / 2, height / 2));
        simulation.force('charge', d3.forceManyBody().strength(-100));
      } else {
        simulation.force('r', null);
        simulation.force('x', d3.forceX(width / 2).strength(0.07));
        simulation.force('y', d3.forceY(height / 2).strength(0.07));
        simulation.force('charge', d3.forceManyBody().strength(-1000));
      }
      
      simulation.alpha(1).restart();
    }
  };

  const setHierarchicalLayout = () => {
    const newIsHierarchical = !isHierarchical;
    setIsHierarchical(newIsHierarchical);
  
    if (!newIsHierarchical) {
      if (simulationRef.current && gRef.current) {
        gRef.current.transition().duration(750).attr('transform', '');
        simulationRef.current.alpha(1).restart();
        const linkSelection = d3.select(svgRef.current).select('g').selectAll<SVGPathElement, SimulationLinkWithType>('path');
        linkSelection.style('opacity', 0.7);
      }
    } else {
      if (gRef.current && data.nodes.length > 0 && simulationRef.current) {
        simulationRef.current.stop();
        
        const currentCollapsed = Array.from(collapsedNodes).sort();
      const cachedCollapsed = positionCache.current.collapsedNodes;
      const useCache = JSON.stringify(currentCollapsed) === JSON.stringify(cachedCollapsed);
      let visibleNodeIds;

      if (useCache) {
        positionCache.current.positions.forEach((pos, id) => {
          const node = data.nodes.find(n => n.id === id);
          if (node) {
            node.x = pos.x;
            node.y = pos.y;
          }
        });
        visibleNodeIds = positionCache.current.visibleNodeIds;
      } else {

        interface TreeNode {
          id: string;
          name: string;
          children: TreeNode[];
          [key: string]: any;
        }
  
        const rootNodes = data.nodes.filter(
          (n) =>
            n.type === 'module' &&
            !data.links.some(
              (l) =>
                l.type === 'contains' &&
                (typeof l.target === 'object' ? l.target.id : l.target) === n.id
            )
        );
  
        function buildTree(node: CodeNode): TreeNode {
          const children = data.links
            .filter(
              (l) =>
                l.type === 'contains' &&
                (typeof l.source === 'object' ? l.source.id : l.source) === node.id &&
                !collapsedNodes.has(node.id)
            )
            .map((l) => {
              const targetId = typeof l.target === 'object' ? l.target.id : l.target;
              return data.nodes.find((n) => n.id === targetId) || ({} as CodeNode);
            });
  
          const result: TreeNode = {
            id: node.id,
            name: node.name,
            children: children.map(buildTree),
          };
  
          Object.keys(node).forEach((key) => {
            if (key !== 'id' && key !== 'name' && key !== 'children') {
              result[key] = (node as any)[key];
            }
          });
  
          return result;
        }
  
        const hierarchicalData: TreeNode = {
          id: 'root',
          name: 'root',
          children: rootNodes.map(buildTree),
        };
  
        const root = d3.hierarchy(hierarchicalData);
        const treeLayout = d3.tree<TreeNode>().size([height * 3, width - 200]);
        treeLayout(root);
  
        root.descendants().forEach((d) => {
          if (!d.data.id) return;
          const original = data.nodes.find((n) => n.id === d.data.id);
          if (original) {
            original.x = d.y;
            original.y = d.x;
          }
        });
  
        data.nodes.forEach((node) => {
          if (!node.x || !node.y) {
            node.x = width / 2;
            node.y = height / 2;
          }
        });

        visibleNodeIds = new Set(root.descendants().map(d => d.data.id));
  
        positionCache.current = {
          collapsedNodes: currentCollapsed,
          positions: new Map(data.nodes.map(node => [node.id, { x: node.x!, y: node.y! }])),
          visibleNodeIds
        };
      }

        // const nodeById = new Map(data.nodes.map((node) => [node.id, node]));
  
        const nodeSelection = d3.select(svgRef.current).select('g').selectAll<SVGGElement, CodeNode>('.node');
        const linkSelection = d3.select(svgRef.current).select('g').selectAll<SVGPathElement, SimulationLinkWithType>('path');
  
        nodeSelection
          .transition()
          .duration(750)
          .attr('transform', (d) => `translate(${d.x ?? 0},${d.y ?? 0})`);
  
          linkSelection
          .transition()
          .duration(750)
          .attr('d', (d) => {
            const source = d.source as SimulationNodeWithType;
            const target = d.target as SimulationNodeWithType;
            const sourceX = source.x ?? 0;
            const sourceY = source.y ?? 0;
            const targetX = target.x ?? 0;
            const targetY = target.y ?? 0;
            if (d.type === 'contains') {
              return `M${sourceX},${sourceY}C${sourceX},${(sourceY + targetY) / 2}${targetX},${(sourceY + targetY) / 2}${targetX},${targetY}`;
            }
            return `M${sourceX},${sourceY}L${targetX},${targetY}`;
          });
  
          linkSelection.style('opacity', (d) => {
            const sourceId = typeof d.source === 'string' ? d.source : d.source.id;
            const targetId = typeof d.target === 'string' ? d.target : d.target.id;
            return d.type === 'contains' && visibleNodeIds.has(sourceId) && visibleNodeIds.has(targetId) ? 0.7 : 0;
          });
  
        const positionedNodes = data.nodes.filter((n) => n.x !== undefined && n.y !== undefined);
        if (positionedNodes.length > 0) {
          const bounds = {
            left: Math.min(...positionedNodes.map((n) => n.x!)),
            right: Math.max(...positionedNodes.map((n) => n.x!)),
            top: Math.min(...positionedNodes.map((n) => n.y!)),
            bottom: Math.max(...positionedNodes.map((n) => n.y!)),
          };
          const centerX = width / 2 - (bounds.left + (bounds.right - bounds.left) / 2);
          const centerY = height / 2 - (bounds.top + (bounds.bottom - bounds.top) / 2);
          gRef.current.transition().duration(750).attr('transform', `translate(${centerX},${centerY})`);
        }
      }
    }
  };
    
  useEffect(() => {
    if (!svgRef.current || data.nodes.length === 0) return;
    
    const svg = d3.select(svgRef.current)
      .attr('width', width)
      .attr('height', height);
    
    svg.selectAll('*').remove();
    
    const defs = svg.append('defs');
    ['imports', 'contains', 'calls'].forEach(type => {
      defs.append('marker')
        .attr('id', `arrow-${type}`)
        .attr('viewBox', '0 -5 10 10')
        .attr('refX', 15)
        .attr('refY', 0)
        .attr('markerWidth', 6)
        .attr('markerHeight', 6)
        .attr('orient', 'auto')
        .append('path')
        .attr('fill', type === 'imports' ? '#7c3aed' : type === 'contains' ? '#059669' : '#ea580c')
        .attr('d', 'M0,-5L10,0L0,5');
    });
  
    const g = svg.append('g');
    gRef.current = g;
    
    const zoom = d3.zoom<SVGSVGElement, unknown>()
      .scaleExtent([0.1, 4])
      .on('zoom', (event) => {
        g.attr('transform', event.transform.toString());
      });
    
    zoomRef.current = zoom;
    svg.call(zoom);
    
    const tooltip = d3.select(containerRef.current)
      .append('div')
      .attr('class', 'absolute p-3 bg-white rounded-lg shadow-md pointer-events-none text-sm max-w-xs opacity-0 z-50')
      .style('z-index', '1000');
    
    const getNodeSize = (node: CodeNode) => {
      if (node.type === 'module') return 12;
      if (node.type === 'class') return 10;
      return 8;
    };
  
    const getNodeFillColor = (node: CodeNode) => {
      if (node.is_entry) return '#fbbf24';
      if (node.metrics && (
        (node.metrics.complexity && node.metrics.complexity > 10) ||
        (node.metrics.cognitive_complexity && node.metrics.cognitive_complexity > 15) ||
        (node.type === 'class' && node.metrics.complexity && node.metrics.complexity > 50)
      )) return '#ef4444';
      return '#fff';
    };
    
    const getNodeStrokeColor = (node: CodeNode) => {
      if (node.type === 'module') return '#0284c7';
      if (node.type === 'class') return '#0891b2';
      return '#0d9488';
    };
    
    const renderMode = data.nodes.length > 5000 ? 'webgl' : 
                      data.nodes.length > 500 ? 'canvas' : 'svg';
    
    const ticksPerRender = data.nodes.length > 5000 ? 20 : 
                           data.nodes.length > 2000 ? 15 :
                           data.nodes.length > 500 ? 10 : 1;
                           
    const initialAlphaDecay = data.nodes.length > 5000 ? 0.15 : 
                             data.nodes.length > 2000 ? 0.12 :
                             data.nodes.length > 1000 ? 0.1 : 0.05;
                             
    const chargeStrength = Math.min(-300, -20000 / Math.sqrt(data.nodes.length));
    
    const simulation = d3.forceSimulation<SimulationNodeWithType>()
    .force('link', d3.forceLink<SimulationNodeWithType, SimulationLinkWithType>().id(d => d.id).distance(d => {
      const sourceType = typeof d.source === 'object' ? d.source.type : 'unknown';
      const targetType = typeof d.target === 'object' ? d.target.type : 'unknown';
      
      if (sourceType === 'module' || targetType === 'module') return 120;
      if (sourceType === 'class' || targetType === 'class') return 80;
      return 60;
    }).strength(d => d.type === 'contains' ? 0.7 : 0.3))
    .force('charge', d3.forceManyBody<SimulationNodeWithType>().strength(chargeStrength).distanceMax(500))
    .force('center', d3.forceCenter<SimulationNodeWithType>(width / 2, height / 2))
    .force('collision', d3.forceCollide<SimulationNodeWithType>().radius(d => {
      return getNodeSize(d as unknown as CodeNode) + (d.name.length * 2);
    }).strength(0.8))
    .force('x', d3.forceX<SimulationNodeWithType>(width / 2).strength(0.07))
    .force('y', d3.forceY<SimulationNodeWithType>(height / 2).strength(0.07))
    .force('cluster', forceCluster())
    .alphaDecay(initialAlphaDecay)
    .velocityDecay(0.4);
    
    simulationRef.current = simulation;
    
    const normalizedNodes = data.nodes.map(node => ({...node}));
    const normalizedLinks = data.links.map(link => ({
      source: typeof link.source === 'object' ? link.source.id : link.source,
      target: typeof link.target === 'object' ? link.target.id : link.target,
      type: link.type
    }));
    
    const quadtree = d3.quadtree<any>()
      .x(d => d.x)
      .y(d => d.y)
      .addAll(normalizedNodes);
    
    if (renderMode === 'webgl' || renderMode === 'canvas') {
      const canvas = document.createElement('canvas');
      canvas.width = width;
      canvas.height = height;
      const context = canvas.getContext('2d', {alpha: true})!;
      
      svgRef.current.parentNode?.appendChild(canvas);
      
      const nodePositions = new Float32Array(normalizedNodes.length * 2);
      const nodeColors = new Uint8Array(normalizedNodes.length * 4);
      const nodeSizes = new Float32Array(normalizedNodes.length);
      
      const updateNodeArrays = () => {
        normalizedNodes.forEach((node, i) => {
          nodePositions[i*2] = node.x || 0;
          nodePositions[i*2+1] = node.y || 0;
          
          const size = getNodeSize(node);
          nodeSizes[i] = size;
          
          const fillColor = getNodeFillColor(node);
          const r = parseInt(fillColor.substr(1, 2), 16);
          const g = parseInt(fillColor.substr(3, 2), 16);
          const b = parseInt(fillColor.substr(5, 2), 16);
          
          nodeColors[i*4] = r;
          nodeColors[i*4+1] = g;
          nodeColors[i*4+2] = b;
          nodeColors[i*4+3] = 255;
        });
      };
      
      const renderVisibleNodes = () => {
        const transform = d3.zoomTransform(svg.node()!);
        const zoomLevel = transform.k;
      
        context.clearRect(0, 0, width, height);
        context.save();
        context.translate(transform.x, transform.y);
        context.scale(transform.k, transform.k);
      
        normalizedLinks.forEach(link => {
          const sourceNode = normalizedNodes.find(n => n.id === link.source);
          const targetNode = normalizedNodes.find(n => n.id === link.target);
          if (!sourceNode || !targetNode) return;
      
          context.beginPath();
          context.moveTo(sourceNode.x || 0, sourceNode.y || 0);
          context.lineTo(targetNode.x || 0, targetNode.y || 0);
          context.strokeStyle = link.type === 'imports' ? '#7c3aed' : '#059669';
          context.lineWidth = 1.5;
          context.stroke();
        });
      
        normalizedNodes.forEach(node => {
          const size = getNodeSize(node);
          context.beginPath();
          context.arc(node.x || 0, node.y || 0, size, 0, 2 * Math.PI);
          context.fillStyle = getNodeFillColor(node);
          context.strokeStyle = getNodeStrokeColor(node);
          context.fill();
          context.stroke();
      
          if (zoomLevel > 0.5) { 
            context.fillStyle = '#333';
            context.font = '12px Segoe UI';
            context.fillText(node.name, (node.x || 0) + size + 2, (node.y || 0) + 4);
          }
        });
      
        context.restore();
      };

      let tickCounter = 0;
      simulation.on('tick', () => {
        tickCounter++;
        if (tickCounter % ticksPerRender !== 0) return;
        
        updateNodeArrays();
        renderVisibleNodes();
      });
      
      canvas.addEventListener('mousemove', (event) => {
        const transform = d3.zoomTransform(svg.node()!);
        const x = (event.offsetX - transform.x) / transform.k;
        const y = (event.offsetY - transform.y) / transform.k;
        
        const radius = 10 / transform.k;
        const node = quadtree.find(x, y, radius);
        
        if (node) {
          const connections = normalizedLinks.filter(l => 
            l.source === node.id || l.target === node.id
          );
          
          let tooltipContent = `<strong>${node.name}</strong> <span style="color: #666;">(${node.type})</span><br>`;
          
          const hasQualityIssues = node.code_smells && node.code_smells.length > 0;
          const hasComplexity = node.metrics && (
            (node.metrics.complexity && node.metrics.complexity > 10) || 
            (node.metrics.cognitive_complexity && node.metrics.cognitive_complexity > 15)
          );
          
          if (hasQualityIssues || hasComplexity) {
            tooltipContent += '<div style="margin: 5px 0; padding: 5px; background: #fee2e2; border-left: 4px solid #ef4444; border-radius: 3px;">';
            tooltipContent += '<strong style="color: #b91c1c;">⚠️ Issues Detected</strong>';
            tooltipContent += '</div>';
          }
          
          if (connections.length > 0) {
            tooltipContent += `<div><strong style="color: #0891b2;">Connections:</strong> ${connections.length}</div>`;
          }
          
          tooltip.html(tooltipContent)
            .style('left', `${event.pageX + 10}px`)
            .style('top', `${event.pageY - 10}px`)
            .transition()
            .duration(200)
            .style('opacity', 0.9);
        } else {
          tooltip.transition()
            .duration(500)
            .style('opacity', 0);
        }
      });
      
      canvas.addEventListener('mouseleave', () => {
        tooltip.transition()
          .duration(500)
          .style('opacity', 0);
      });
      
      canvas.addEventListener('click', (event) => {
        const transform = d3.zoomTransform(svg.node()!);
        const x = (event.offsetX - transform.x) / transform.k;
        const y = (event.offsetY - transform.y) / transform.k;
        
        const radius = 10 / transform.k;
        const node = quadtree.find(x, y, radius);
        
        if (node) {
          navigate(`/node/${node.id}`);
        }
      });
      
      simulation.nodes(normalizedNodes);
      const linkForce = simulation.force('link') as d3.ForceLink<any, any>;
      if (linkForce) {
        linkForce.links(normalizedLinks);
      }
      
      if (data.nodes.length > 300) {
        setTimeout(() => {
          simulation.alpha(0).stop();
        }, 3000);
      }
      
      return () => {
        simulation.stop();
        if (canvas && canvas.parentNode) {
          canvas.parentNode.removeChild(canvas);
        }
        if (tooltip) tooltip.remove();
      };
    } else {
      const linkSelection = g.append('g').selectAll('path')
        .data(normalizedLinks)
        .join('path')
        .attr('class', d => `link link-${d.type}`)
        .attr('marker-end', d => `url(#arrow-${d.type})`)
        .attr('stroke-width', '1.5px')
        .attr('fill', 'none')
        .style('opacity', 0.7)
        .style('stroke', d => {
          if (d.type === 'imports') return '#7c3aed';
          if (d.type === 'contains') return '#059669';
          return '#ea580c';
        });
      
      const nodeSelection = g.append('g').selectAll('.node')
        .data(normalizedNodes)
        .join('g')
        .attr('class', d => `node node-${d.type}`)
        .style('cursor', 'pointer');
      
      const dragBehavior = d3.drag<any, any>()
        .on('start', (event, d) => {
          if (!event.active) simulation.alphaTarget(0.3).restart();
          d.fx = d.x;
          d.fy = d.y;
        })
        .on('drag', (event, d) => {
          d.fx = event.x;
          d.fy = event.y;
        })
        .on('end', (event, d) => {
          if (!event.active) simulation.alphaTarget(0);
          d.fx = null;
          d.fy = null;
        });
      
      nodeSelection.call(dragBehavior);

      nodeSelection.on('click', (_, d) => {
        if (isHierarchical && (d.type === 'module' || d.type === 'class')) {
          const newCollapsed = new Set(collapsedNodes);
          if (newCollapsed.has(d.id)) {
            newCollapsed.delete(d.id);
          } else {
            newCollapsed.add(d.id);
          }
          setCollapsedNodes(newCollapsed);
          setHierarchicalLayout();
        } else {
          navigate(`/node/${d.id}`);
        }
      });
          
      nodeSelection.append('circle')
        .attr('r', getNodeSize)
        .style('fill', getNodeFillColor)
        .style('stroke', getNodeStrokeColor)
        .style('stroke-width', '2px');
      
      if (data.nodes.length < 500) {
        nodeSelection.append('rect')
          .attr('class', 'text-background')
          .attr('fill', 'white')
          .attr('fill-opacity', 0.9)
          .attr('rx', 3)
          .attr('ry', 3);
          
        nodeSelection.append('text')
          .attr('dy', '.35em')
          .attr('x', d => getNodeSize(d) + 8)
          .attr('class', 'node-text')
          .text(d => d.name)
          .style('font', '12px "Segoe UI", sans-serif')
          .style('font-weight', '500')
          .style('text-anchor', 'start')
          .style('fill', '#333')
          .each(function() {
            const bbox = (this as SVGTextElement).getBBox();
            const parent = this.parentNode as Element;
            d3.select(parent)
              .select('rect.text-background')
              .attr('x', bbox.x - 4)
              .attr('y', bbox.y - 2)
              .attr('width', bbox.width + 8)
              .attr('height', bbox.height + 4);
          });
      }
            
      nodeSelection.on('mouseover', function(event, d) {
        const connections = normalizedLinks.filter(l => 
          l.source === d.id || l.target === d.id
        );
        
        let tooltipContent = `<strong>${d.name}</strong> <span style="color: #666;">(${d.type})</span><br>`;
        
        const hasQualityIssues = d.code_smells && d.code_smells.length > 0;
        const hasComplexity = d.metrics && (
          (d.metrics.complexity && d.metrics.complexity > 10) || 
          (d.metrics.cognitive_complexity && d.metrics.cognitive_complexity > 15)
        );
        
        if (hasQualityIssues || hasComplexity) {
          tooltipContent += '<div style="margin: 5px 0; padding: 5px; background: #fee2e2; border-left: 4px solid #ef4444; border-radius: 3px;">';
          tooltipContent += '<strong style="color: #b91c1c;">⚠️ Issues Detected</strong>';
          tooltipContent += '</div>';
        }
        
        if (connections.length > 0) {
          tooltipContent += `<div><strong style="color: #0891b2;">Connections:</strong> ${connections.length}</div>`;
        }
        
        tooltip.html(tooltipContent)
          .style('left', `${event.pageX + 10}px`)
          .style('top', `${event.pageY - 10}px`)
          .transition()
          .duration(200)
          .style('opacity', 0.9);
        
        const connectedNodeIds = new Set<string>([d.id]);
        connections.forEach(c => {
          connectedNodeIds.add(c.source);
          connectedNodeIds.add(c.target);
        });
        
        nodeSelection.style('opacity', n => connectedNodeIds.has(n.id) ? 1 : 0.1);
        
        linkSelection.style('opacity', l => 
          (l.source === d.id || l.target === d.id) ? 1 : 0.1
        );
      })
      .on('mouseout', function() {
        tooltip.transition()
          .duration(500)
          .style('opacity', 0);
        
        nodeSelection.style('opacity', 1);
        linkSelection.style('opacity', 0.7);
      })
      .on('click', (_, d) => {
        navigate(`/node/${d.id}`);
      });
      
      let tickCounter = 0;
  
      const ticked = () => {
        tickCounter++;
        if (tickCounter % ticksPerRender !== 0) return;
      
        linkSelection.attr('d', (d: any) => {
          const source = d.source;
          const target = d.target;
          return `M${source.x},${source.y}L${target.x},${target.y}`;
        });
      
        nodeSelection.attr('transform', (d) => {
          positionCache.current.positions.set(d.id, { x: d.x!, y: d.y! }); 
          return `translate(${d.x!},${d.y!})`; 
        });
      
        quadtree.removeAll(normalizedNodes).addAll(normalizedNodes);
      };
      
      simulation
        .nodes(normalizedNodes)
        .on('tick', ticked);
      
      const linkForce = simulation.force('link') as d3.ForceLink<any, any>;
      if (linkForce) {
        linkForce.links(normalizedLinks);
      }
      
      if (data.nodes.length > 300) {
        setTimeout(() => {
          simulation.alpha(0).stop();
        }, 3000);
      }
      
      return () => {
        simulation.stop();
        if (tooltip) tooltip.remove();
      };
    }
  }, [data, width, height, navigate]);
  
  const handleSearchChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const term = e.target.value;
    setSearchTerm(term);
    
    if (!svgRef.current || !term) return;
    
    const svg = d3.select(svgRef.current);
    const nodeSelection = svg.selectAll<SVGGElement, CodeNode>('.node');
    const linkSelection = svg.selectAll<SVGPathElement, SimulationLinkWithType>('path');
    
    if (term === "") {
      nodeSelection.style("opacity", 1);
      linkSelection.style("opacity", 0.7);
      return;
    }
    
    const lowerTerm = term.toLowerCase();
    
    if (data.nodes.length > 1000) {
      const nodeMap = nodeMapRef.current;
      const matchedNodes = Array.from(nodeMap.entries())
        .filter(([name]) => name.includes(lowerTerm))
        .map(([, node]) => node);
      
      const matchedNodeIds = new Set(matchedNodes.map(node => node.id));
      const connectedNodes = new Set(matchedNodeIds);
      
      data.links.forEach(link => {
        const sourceId = typeof link.source === 'object' ? link.source.id : link.source;
        const targetId = typeof link.target === 'object' ? link.target.id : link.target;
        
        if (matchedNodeIds.has(sourceId)) connectedNodes.add(targetId);
        if (matchedNodeIds.has(targetId)) connectedNodes.add(sourceId);
      });
      
      nodeSelection.style("opacity", d => connectedNodes.has(d.id) ? 1 : 0.1);
      linkSelection.style("opacity", d => {
        const sourceId = typeof d.source === 'string' ? d.source : d.source.id;
        const targetId = typeof d.target === 'string' ? d.target : d.target.id;
        
        return connectedNodes.has(sourceId) && connectedNodes.has(targetId) ? 0.7 : 0.1;
      });
    } else {
      const matchedNodes = new Set<string>();
      
      nodeSelection.each(function(d) {
        const node = d as CodeNode;
        if (node.name.toLowerCase().includes(lowerTerm)) {
          matchedNodes.add(node.id);
          
          data.links.forEach(l => {
            const sourceId = typeof l.source === 'object' ? l.source.id : l.source;
            const targetId = typeof l.target === 'object' ? l.target.id : l.target;
            
            if (sourceId === node.id) matchedNodes.add(targetId);
            if (targetId === node.id) matchedNodes.add(sourceId);
          });
        }
      });
      
      nodeSelection.style("opacity", d => matchedNodes.has(d.id) ? 1 : 0.1);
      
      linkSelection.style("opacity", d => {
        const sourceId = typeof d.source === 'string' ? d.source : d.source.id;
        const targetId = typeof d.target === 'string' ? d.target : d.target.id;
        
        return matchedNodes.has(sourceId) && matchedNodes.has(targetId) ? 0.7 : 0.1;
      });
    }
  };
  
  if (loading) {
    return (
      <div className="flex items-center justify-center h-screen">
        <div className="text-xl">Loading visualization data...</div>
      </div>
    );
  }
  
  if (error) {
    return (
      <div className="flex items-center justify-center h-screen flex-col">
        <div className="text-xl text-red-600 mb-4">Error loading data</div>
        <div className="text-gray-700">{error}</div>
        <button 
          onClick={() => window.location.reload()}
          className="mt-6 bg-blue-600 text-white px-4 py-2 rounded hover:bg-blue-700 transition-colors"
        >
          Retry
        </button>
      </div>
    );
  }
  
  return (
    <div ref={containerRef} className="relative w-full h-screen overflow-hidden bg-gray-50">
      <div className="fixed top-5 left-5 bg-white p-4 rounded-xl shadow-md z-10 flex flex-wrap gap-4 items-center">
        <div>
          <input 
            type="text" 
            id="search"
            placeholder="Search nodes..." 
            value={searchTerm}
            onChange={handleSearchChange}
            className="w-64 p-2 border border-gray-200 rounded-md text-sm"
          />
        </div>
        <div className="flex gap-2">
          <button 
            onClick={resetZoom}
            className="bg-blue-700 text-white border-none px-4 py-2 rounded-md cursor-pointer text-sm transition-colors hover:bg-blue-800"
          >
            Reset View
          </button>
          <button 
            onClick={toggleLayout}
            className="bg-blue-700 text-white border-none px-4 py-2 rounded-md cursor-pointer text-sm transition-colors hover:bg-blue-800"
          >
            Toggle Layout
          </button>
          <button 
            onClick={setHierarchicalLayout}
            className="bg-blue-700 text-white border-none px-4 py-2 rounded-md cursor-pointer text-sm transition-colors hover:bg-blue-800"
          >
            {isHierarchical ? 'Force Layout' : 'Hierarchical View'}
          </button>
        </div>
      </div>
      
      <div className="fixed bottom-5 left-5 bg-white p-4 rounded-xl shadow-md z-10">
        <h3 className="mt-0 mb-3 text-base text-gray-900">Legend</h3>
        <div className="flex items-center mb-2 text-sm">
          <div className="w-4 h-4 rounded bg-blue-600 mr-2"></div>
          <span>Module</span>
        </div>
        <div className="flex items-center mb-2 text-sm">
          <div className="w-4 h-4 rounded bg-amber-400 mr-2"></div>
          <span>Entry Point</span>
        </div>
        <div className="flex items-center mb-2 text-sm">
          <div className="w-4 h-4 rounded bg-cyan-600 mr-2"></div>
          <span>Class</span>
        </div>
        <div className="flex items-center mb-2 text-sm">
          <div className="w-4 h-4 rounded bg-teal-600 mr-2"></div>
          <span>Function</span>
        </div>
        <div className="mt-4 border-t border-gray-200 pt-3">
          <div className="flex items-center mb-2 text-sm">
            <div className="w-10 h-0.5 bg-purple-600 mr-2"></div>
            <span>Imports</span>
          </div>
          <div className="flex items-center mb-2 text-sm">
            <div className="w-10 h-0.5 bg-green-600 mr-2"></div>
            <span>Contains</span>
          </div>
          <div className="flex items-center mb-2 text-sm">
            <div className="w-10 h-0.5 bg-orange-600 mr-2"></div>
            <span>Calls</span>
          </div>
        </div>
      </div>
      
      <svg ref={svgRef} id="visualization" className="w-full h-screen"></svg>
    </div>
  );
};

export default VisualizationPage;